@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&family=Rubik:wght@300;400;500&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
}

body, html {
    height: auto;
    width: 100vw;
    background-color: rgb(233, 231, 231);
    /* overflow-x: hidden; */

}

/* Header CSS */

.header-div {

    background-color: darkblue;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;

}

.header-row {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.list-item {
    text-decoration: none;
    color: white;
}


.list-item:hover {
    cursor: pointer;
    color: black;


}

/* contact js Css  */
.main-form-div {
    width: 50%;
    border: 1px solid rgb(142, 140, 140);
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    margin-top: 20px;
    border-radius: 8px;

}

form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin: 050px;
}

.form-heading {
    color: darkblue;
}

.input-field {
    padding: 15px;
    width: 90%;
    border-radius: 8px;

    height: 45px;
    border: none;
    outline: none;
    background-color: rgb(233, 231, 231);
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.btn-class {
    width: 150px;
    height: 35px;
    border-radius: 5px;
    background-color: darkblue;
    color: white;
    border: none;
}

.btn-class:hover {
    background-color: rgb(233, 231, 231);

    color: darkblue;
    border: 2px solid darkblue;
}

/* user page select sort css  */
.select-option {
    background-color: rgb(233, 231, 231);
    border: none;
    outline: none;
}


.table-div {
    width: 100%;
}


@media (max-width:1000px) and (min-width: 350px) {
    .main-form-div {
        width: 90%;


        margin-top: 20px;
        border-radius: 8px;

    }

    form {
        min-height: 50vh;
        width: 90%;
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: center;
        gap: 30px;

    }

    .input-field {
        padding: 15px;
        width: 100%;
        border-radius: 8px;

        height: 45px;
        border: none;
        outline: none;
        background-color: rgb(233, 231, 231);
        box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    }

}